.post__container{
   width: 100%;
   padding-top: 15px;
}
.post__container h2{
    padding-left: 5px;
    color: white;
}
.movie__post{
    width: 100%;
    padding-top: 10px;
    padding-left: 20px;
    overflow-y: hidden;
    overflow-x: visible;
    display: flex;
    transition: all 1s ease-in-out;
    
}
.netflix__original{
    width: 100%;
    padding-top: 10px;
    padding-left: 20px;
    overflow-y: hidden;
    overflow-x: visible;
    display: flex;
    transition: all 1s ease-in-out;
}
.movie__post::-webkit-scrollbar{
    display: none;
}
.netflix__original::-webkit-scrollbar{
    display: none;
}
.movie__post div{
    padding-left:10px ;
}
.netflix__original div{
    padding-left: 10px;;
}
.movie__post img{
    height: 80px;
    width: 100px;
    
}

.netflix__original img{
    height: 120px;
    width: 100px  ;
    object-fit: cover; 
}
.movie__post div:hover{
    transform: scale(1.09);
}
.netflix__original div:hover{
    transform: scale(1.10);
}
.video__player{
    position: fixed;
    bottom: 5%;
}