.banner__container{
    color: white;
   
    height:448px;
}
.banner__content{
    margin-left: 30px;
    padding-top: 140px;
    height: 180px;

}
.banner__title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.banner__description{
    width:45rem;
    line-height: 1.3;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
}
.banner__btn{
    color: #fff;
    cursor: pointer;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.8vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    background-color: rgba(51,51,51,0.5);
    padding-bottom: 0.5rem;
}
.banner__bottom{
    height: 7.4rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,0.61),#111);
    margin-top: 150px;
}
.video__player{
    position: fixed;
    bottom: 5%;
}